.boton-clear {
  height: 80px;
  font-size: 1.6em;
  display: flex;
  flex: 1;
  background-color: rgb(53, 76, 86);
  box-shadow: 0px 2px 5px 2px white;
  margin-top: 8px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: white;
  border: 2px solid white;
  border-radius: 4px;
  cursor: pointer;
  user-select: none;
}

.boton-clear:hover {
  background-color: rgb(65, 102, 125);
}
